.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-container .button {
  text-align: center;
  background-color: #699;
  display: flex;
  flex-direction: column;
  width: 16vw;
  border-style: thin;
  border-color: #000;
  border-radius: 4px;
  color: #000;
  padding-top: 1vh;
  padding-bottom: 1vh;
  margin-bottom: 1vh;
  background-color: #9fc;
  cursor: pointer;
  font-size: 0.6em;
}

.about-container .button:hover {
  background: #66ffb3;
}

.about-container p a {
  color: #000;
  font-weight: bold;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}